* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-bg-color: #1b1a20;
}

body {
  background-color: var(--main-bg-color);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.link {
  text-decoration: none;
  color: white;
}

main h1 {
  text-align: center;
  font-size: clamp(3rem, 5vw, 10rem);
  padding: 2rem 2rem;
}

section {
  display: grid;
  padding: 0 20rem 10rem 20rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-items: center;
  justify-content: center;
  row-gap: 4rem;
}

.card {
  border-radius: 15px;
  overflow: hidden;
}

/* Single Movie Page */

.backdrop {
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: var(--main-bg-color) !important;
  position: absolute;
}

.movie__card {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 300px;
  flex-direction: column;
  flex-shrink: 1;
}

.movie__description {
  width: 100%;
  max-width: 800px;
  font-size: 1.5rem;
  padding: 2rem;
  border-radius: 15px;
  background-color: #211e25;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.movie__description p {
  font-size: 2rem;
  order: 2;
  line-height: 50px;
}

.movie__poster {
  order: 1;
  width: 250px;
  margin: 1rem 0rem 5rem 0rem;
  border-radius: 30px;
}

.release-date {
  margin-top: 0.5rem;
  text-align: right;
  color: #e4e4e4;
  font-size: 0.8rem;
}

.move__card .movie__title-section > h1 {
  font-size: clamp(3rem, 5vw, 10rem);
}

.movie__title-section {
  padding: 3rem;
}

@media (max-width: 700px) {
  main h1 {
    padding: 4rem 2rem 4rem 2rem;
  }
  section {
    padding: 0;
    padding-bottom: 4rem;
  }
}

@media (min-width: 1300px) {
  .movie__card {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: column;
  }

  .movie__description {
    background-color: #211e25;
    min-width: 600px;
    width: 800px;
    font-size: 1.5rem;
    padding: 4rem 4rem 10rem 4rem;
    position: relative;
    border-radius: 15px;
  }

  .movie__title-section {
    position: relative;
    right: 10%;
    width: 500px;
    padding: 5rem 3rem;
  }

  .release-date {
    text-align: right;
    color: #e4e4e4;
    font-size: 0.8rem;
  }

  .movie__poster {
    position: absolute;
    right: -220px;
    width: 250px;
    border-radius: 30px;
    top: -150px;
  }
}
